
body {
  margin: 0px;
  padding: 0px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

#img {
    width: 100%;
    height: 150px;
}
#box {
    width: 100%;
}
#input-box {
    margin-top: 5%;
    margin-left: 20%;
    margin-right: 20%;
}
input::-webkit-input-placeholder {
    text-align: center;
}
input[type='text'] {
    border-style: solid;
    border-radius: 15px;
    border-width: 1px;
    border-color: gray;
}
#i-phone {
    display: block;
    width: 100%;
    height: 35px;
}
#i-code {
    float: left;
    width: 50%;
    height: 25px;
    display: block;
    margin-top: 5%;
}
#s-send {
    float: right;
    width: 40%;
    height: 25px;
    line-height: 25px;
    background: rgb(35, 161, 245);
    color: white;
    font-size: 12px;
    display: block;
    margin-top: 5%;
    border-style: solid;
    border-radius: 15px;
    text-align: center;
}
